@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap');

.cover {
  display: flex;
  flex-direction: column;
  font-family: 'Playfair Display', serif;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100%;
  width: 100%;
  background: #2c2f3f; /* Fondo oscuro */
  border: 3px solid #b44c53; /* Rojo oscuro */
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3); /* Sombra más fuerte */
}

.cover h1 {
  font-size: 2.5rem;
  font-family: 'Playfair Display', serif;
  color: #f4c6c4; /* Tono suave pero oscuro para el título */
  margin-bottom: 20px;
}

.cover p {
  font-size: 1.2rem;
  font-family: 'Playfair Display', serif;
  color: #d1d1d1; /* Gris suave */
  margin-bottom: 30px;
}

.cover button {
  margin-bottom: 20px;
  font-size: 1.2rem;
  font-family: 'Playfair Display', serif;
  padding: 15px 30px;
  border-radius: 50px;
  background-color: #b44c53; /* Rojo oscuro para el botón */
  color: white; /* Texto blanco para el botón */
  border: none;
  transition: background-color 0.3s ease;
}

.cover button:hover {
  font-family: 'Playfair Display', serif;
  background-color: #8f3e44; /* Rojo más oscuro cuando se pasa el cursor */
}