@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Playfair Display', serif;
  background: linear-gradient(to bottom, #2c2f3f, #1f232b); /* Fondo oscuro degradado */
  color: #d1d1d1; /* Texto gris suave */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}

button {
  cursor: pointer;
  font-size: 1rem;
  padding: 10px 20px;
  border: none;
  font-family: 'Playfair Display', serif;
  border-radius: 5px;
  background-color: #b44c53; /* Rojo oscuro */
  color: white;
  transition: all 0.3s ease;
}

button:hover {
  background-color: #8f3e44; /* Rojo más oscuro al pasar el cursor */
}