@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap');

/* Ahora, aplica la fuente a tu sitio */
body {
  margin: 0;
  padding: 0;
  font-family: 'Playfair Display', serif;
  background: linear-gradient(135deg, #2c2f3f, #4a4f63); /* Degradado oscuro elegante */
  color: #d1d1d1; /* Gris claro para el texto general */
}

/* Libro */
.book {
  position: relative;
  user-select: none;
  width: 800px;
  height: 500px;
  margin: auto;
  border: 3px solid #b44c53; /* Borde oscuro elegante */
  background-color: #2c2f3f; /* Fondo oscuro */
  border-radius: 15px; /* Bordes más redondeados */
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.5); /* Sombra más intensa */
}

/* Contenedor del libro */
.book-container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; /* Centrar las páginas */
}

/* Páginas */
.page {
  position: absolute;
  width: 50%;
  height: 100%;
  background-image: url("../assets/pagina.png");
  background-size: cover;
  background-position: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Sombra más intensa */
  border-radius: 10px;
  overflow: hidden;
  backface-visibility: hidden; /* Ocultar el reverso */
  display: flex;
  flex-direction: column; /* Para alinear la foto y el texto verticalmente */
  justify-content: center; /* Centrar verticalmente el contenido */
  align-items: center;
  padding: 20px; /* Espaciado interno */
  box-sizing: border-box; /* Incluye el padding en el tamaño total */
  gap: 15px; /* Espaciado entre la foto y el texto */
}

/* Página izquierda estática (página 1) */
.left-page {
  left: 0;
  z-index: 1; /* Siempre debajo de las páginas */
}

/* Página derecha (página 2) */
.right-page {
  right: 0;
  z-index: 2; /* Aparece encima de la página 4 */
}

/* Página izquierda cubriendo (página 3) */
.left-page-cover {
  left: 0;
  z-index: 4; /* Aparece encima de la página 1 */
  backface-visibility: hidden;
}

/* Foto de la página */
.page-photo {
  width: 70%; /* Tamaño más ajustado */
  height: auto; /* Mantener la proporción */
  object-fit: cover;
  border-radius: 10px;
  border: 3px solid #b44c53; /* Marco elegante para la foto */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.page-photo:hover {
  transform: scale(1.1); /* Agrandar al pasar el mouse */
  box-shadow: 0 0 15px 5px #f8a5c2; /* Efecto de iluminación */
}

/* Texto de la página */
.page-phrase {
  font-size: 1.5rem;
  color: #000000; /* Gris claro para el texto */
  text-align: center;
  padding: 10px 20px;
  border-radius: 8px;
  margin-top: 0; /* Sin margen superior */
}

/* Placeholder para páginas vacías */
.page-placeholder {
  font-size: 1.2rem;
  color: #999;
  text-align: center;
  padding: 20px;
}

/* Controles del libro */
.book-controls {
  position: relative;
  bottom: -50px; /* Mueve los botones debajo del libro */
  display: flex;
  justify-content: center;
  gap: 20px; /* Espacio entre botones */
  z-index: 10; /* Asegura que los botones estén encima del contenido */
}

.book-controls button {
  font-size: 1.2rem;
  color: #f4c6c4; /* Tono suave de rojo para los botones */
  background-color: transparent; /* Sin fondo */
  border: 2px solid #b44c53; /* Borde rojo oscuro */
  padding: 10px 20px;
  border-radius: 25px;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease, color 0.2s ease;
}

.book-controls button:hover {
  transform: scale(1.1); /* Agrandar al pasar el mouse */
  color: #ef4444; /* Rojo más oscuro al pasar el mouse */
}

.book-controls button:active {
  box-shadow: 0 0 10px #f87171; /* Efecto de brillo en rojo */
  transform: scale(1.05); /* Reducir ligeramente el tamaño al presionar */
}

.book-controls button:disabled {
  color: #d1d5db;
  cursor: not-allowed;
}




.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center; /* Centrar verticalmente */
  justify-content: center; /* Centrar horizontalmente */
  z-index: 1000;
}

.modal-content {
  position: relative;
  display: flex;
  align-items: center; /* Asegura el centrado vertical de la imagen */
  justify-content: center; /* Asegura el centrado horizontal de la imagen */
  max-width: 90%; /* Limita el tamaño máximo del contenedor */
  max-height: 90%; /* Limita el tamaño máximo del contenedor */
}

.modal-image {
  max-width: 50%; /* Ajusta el ancho máximo al 70% del viewport */
  max-height: auto; /* Ajusta la altura máxima al 70% del viewport */
  width: auto; /* Mantiene la proporción de la imagen */
  height: auto; /* Mantiene la proporción de la imagen */
  border-radius: 8px; /* Bordes redondeados opcionales */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Agrega un efecto de sombra para destacar la imagen */
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
}
